:root {
    --main-light-sec: #04080F;
    --main-light-prim: #28AFB0;
    --main-dark-prim: hsl(207, 8%, 21%);
    --main-dark-sec: #DB504A;
    --main-blue-prim: #333D79FF;
    --main-blue-sec: #FAEBEFFF;
    --main-white-ALWAYS: #fff
}

html[data-theme='light'] {
    --main-black: #04080F;
    --main-blue: #28AFB0;
    --main-Background: #dbe8ec;
    --main-white: #fff;
    /* About */

    /* Plates */
    --plate-1-lg: linear-gradient(270deg, rgba(36, 110, 185, 1) 11%, rgba(75, 198, 185, 0.8) 94%);
    --plate-2-lg: linear-gradient(270deg, rgba(27, 154, 170, 1) 11%, rgba(6, 214, 160, 0.8) 94%);
    --plate-3-lg: linear-gradient(270deg, rgba(63, 136, 197, 1) 11%, rgba(147, 225, 216, 0.8) 94%);
    /* Plate Backgrounds */
    --plate-1-bg: rgba(36, 110, 183, 0.5);
    --plate-2-bg: rgba(27, 154, 170, 0.5);
    --plate-3-bg: rgba(63, 136, 197, 0.5);
    /* Post */
    --box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.09);
    --main-grey: rgba(0, 0, 0, 0.6);
}

html[data-theme='dark'] {
    --main-black: rgb(223, 225, 218);
    --main-blue: #DB504A;
    --main-Background: #32373B;
    --main-grey: rgb(223, 225, 218, 0.6);
    --main-white: #000;
    /* About */

    /* Plates */
    --plate-1-lg: linear-gradient(270deg, rgba(255, 51, 31, 1) 11%, rgba(255, 51, 31, 0.4) 94%);
    --plate-2-lg: linear-gradient(270deg, rgba(164, 3, 31, 1) 11%, rgba(164, 3, 31, 0.4) 94%);
    --plate-3-lg: linear-gradient(270deg, rgba(158, 42, 43, 1) 11%, rgba(158, 42, 43, 0.4) 94%);
    /* Plate Backgrounds */
    --plate-1-bg: rgba(107, 13, 4, 0.5);
    --plate-2-bg: rgba(164, 3, 31, 0.5);
    --plate-3-bg: rgba(158, 42, 43, 0.5);
    /* Post */
    --box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.09);
}

html[data-theme='blue'] {
    --main-black: rgb(223, 225, 218);
    --main-blue: #FAEBEFFF;
    --main-Background: #333D79FF;
    --main-grey: rgb(223, 225, 218, 0.6);
    --main-white: #000;
    /* About */

    /* Plates */
    --plate-1-lg: linear-gradient(270deg, rgba(0, 63, 145, 1) 11%, rgba(0, 63, 145, 0.6) 94%);
    --plate-2-lg: linear-gradient(270deg, rgba(0, 18, 66, 1) 11%, rgba(0, 18, 66, 0.6) 94%);
    --plate-3-lg: linear-gradient(270deg, rgb(0, 0, 49) 11%, rgba(0, 0, 34, 0.6) 94%);
    /* Plate Backgrounds */
    --plate-1-bg: rgba(0, 63, 145, 0.5);
    --plate-2-bg: rgba(0, 18, 66, 0.5);
     --plate-3-bg: rgba(7, 7, 82, 0.5);
    /* Post */
    --box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.09);
}
* {
    margin: none;
    padding: none;
    box-sizing: border-box;
    /* overflow: hidden; */
}

body {
    font-family: 'Source Sans Pro', sans-serif;
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: var(--main-Background);
    /* background: url('./../img/background.jpg'); */
    overflow-x: hidden;
    transition: 1s cubic-bezier(0.77, 0, 0.175, 1);
}

.background-home {
    
}
.container--home{
    display: grid;
    height: 120vh;
    width :100vw;
    margin: 0vw;
    z-index: 0;
    position: relative;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(12, 1fr);
}

.container--item{
    font-size:  6rem;
    color :var(--main-black);
    cursor: pointer;
}

.header-1{
    text-align: center;
    grid-column: 5/9;
    grid-row: 4/5;
    /* font-size: 4rem; */
    font-weight: bold;
}

.header-2{
    text-align: center;
    grid-column: 4/10;
    grid-row: 5/6;
    /* font-size: 4rem; */
    font-weight: bold;
}

.headline {
    text-align: center;
    grid-column: 2/10;
    grid-row: 6/7;
    font-size: 8rem;
    font-weight: normal;
}

.special {
    color : var(--main-blue);
}

.container--icons{  
    display: flex;
    transform: translate(-75px, 0px);
    grid-row: 7/8;
    grid-column: 7/10;
    
}

.icon--github {
    width: 50px;
    margin: 1em;
}

.icon--linkedin {
    width: 50px;
    margin: 1em;
}

@media screen and (max-width : 850px){
    .container--home{}
    .header-1{
        grid-column: 1/-1;
    }
    .header-2{
        grid-column: 1/-1;
    }
    .container--item{
        font-size: 8vh;
    }
    .headline {
        font-size : 9vh;
        grid-column: 1/10;
    }
}

@media screen and (max-width : 400px){
    .container--home{}
    .container--item{
        font-size: 4vh;
    }
    .headline {
        font-size: 4vh;
    }
    .header-1{
        grid-column: 1/-1;
    }
    .header-2{
        grid-column: 1/-1;
    }

}


