    flex-direction: column;
    align-items: center;
    transition: 0.5s ease-in-out;
}

.post--item:hover {
    /* transform: scale(1.009); */
}

.post--img {
    width: 100%;
    padding-bottom: 0em;
    opacity: 0.8;
    overflow: hidden;
    border-radius: 2px;
    image-rendering: optimizeSpeed;
}

.post--header {
    display: flex;
    /* background-color: aqua; */
    /* background-color: #2667FF; */
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
