}

.post--title {
    font-size: 1.4rem;
    text-align: left;
    margin: 0em 0em 0em 1em;
    font-weight: bold;
}

.post--mid {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 70%;
    height: 7%;
    margin-top: 0.5em;
    padding-left: 0em;
}

.btn--read {
    cursor: pointer;
    text-align: center;
    padding: 0.3em 0em 0em 0em;
    position: relative;
    width: 50%;
    text-decoration: none;
    transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);
    background-color: transparent;
    border: none;
    outline: none;
    padding: 0.2em 0em 0.2em 0em;
    margin: 0em 0.5em;
    /* border-radius: 3px; */
    border-left: 1px solid var(--main-black);
    border-right: 1px solid var(--main-black);
    font-size: 1rem;
    color: var(--main-black);
}

.btn--read::before, .btn--read::after {
    content: "";
    position: absolute;
    transition: inherit;
    z-index: 0;
    color: var(--main-black);
}

.btn--read:hover {
    /* border: 1px solid black; */
    color: var(--main-white);
    background-color: var(--main-blue);
    transition-delay: 0.5s;
    border-left: 1px solid var(--main-blue);
    border-right: 1px solid var(--main-blue);
}

.btn--read:hover::before {
    transition-delay: 0s;
}

.btn--read:hover::after {
    /* background-color: white; */
    transition-delay: .35s;
    color: var(--main-black);
}

.from-right::before {
    top: 0;
    width: 0;
    height: 100%;
}

.from-right::after {
    top: 0;
    width: 0;
    height: 100%;
}

.from-right::before {
    left: 0;
    border: 1px solid var(--main-blue);
    border-left: 0;
    border-right: 0;
}

.from-right::after {
    right: 0;
}

.from-right:hover::before, .from-right:hover::after {
    width: 100%;
}

